import React, {useEffect, useState} from 'react';
import {ValineCount, ValinePageview, ValinePanel} from 'react-valine';
import {withRouter} from 'react-router-dom';

function ValineComponent(props) {
  const {location} = props;
  const [uniqStr, setUniqStr] = useState(window.location.origin + window.location.pathname);
  const [refresh, setRefresh] = useState(true);
  useEffect(() => {
    setRefresh(false);
    setUniqStr(window.location.origin + location.pathname);
    setTimeout(() => {
      setRefresh(true)
    }, 0);
  }, [location.pathname]);
  return (
    <div className={`ValineContainer`}>
      <hr/>
      {refresh &&
      <div className={`Valine`} style={{color: 'var(--text-primary)', background: 'var(--background-color)'}}>
        <header className={"App-header"}>
        </header>
        {refresh && <ValinePanel uniqStr={uniqStr}/>}
        评论：<ValineCount/>条
        <br/>
        页面浏览量：<ValinePageview/>次
      </div>}
    </div>
  );
}

export default withRouter(ValineComponent);
